body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.nav-link{
  text-decoration: none;
  width: 100%;
}



.App {
  background-color: white;
  height: 100%;
  width: 100%;
  position: absolute;
  justify-content: center;
  align-items: center;
}
.topButtons{
  display: flex;
  justify-content: space-between;  
  position: absolute;
  top: 0;
  left: 0;
  width: 90%;
  z-index:99;
  padding: 5%;
}

.fullScreenBtn{
  margin-left: auto;
}

#roundBtn {
  display: flex;
  background-color: rgb(57, 109, 160);
  border: none;
  color: white;
  padding: 15px;
  font-size: 30px;
  border-radius: 50%;
  height: 100%;
}

.svg-inline--fa {
  width: 1em;
}
.textButton {
  width: 100%;
  align-items: center;
  justify-content: center;
  display: flex;
  width: 100%;
}

#textBtn {
  border: none;
  color: #121212;
  padding: 15px;
  font-size: 25px;
  border-radius: 12px;
  width: 80%;
}
.Home {
  background-color: white;
  height: 100%;
  width: 100%;
  justify-content: center;
  align-items: center;
}

.content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
  grid-gap: 50px;
  gap: 50px;
  height: 100%;
  width: 100%;
}

.menu{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
  grid-gap: 10px;
  gap: 10px;
  width: 100%;
}
